.app {
  margin: 0 auto;
  width: 80%;

  &__settings {
    display: flex;
    margin-top: 10px;
  }

  &__setting {
    margin-right: 10px;
    font-size: 2em;
  }

  &__step,
  &__frame-size,
  &__item-width,
  &__animation-duration,
  &__frame-infinite {
    font-size: 1em;
    margin-bottom: 20px;
  }

  &__infinite {
    width: 30px;
  }
}
